.footer {
  padding-top: toRem(100);
  padding-bottom: toRem(145);
  border-top: 1px solid #eef2f6;

  @media (max-width: toEm(1024)) {
    padding-top: toRem(80);
    padding-bottom: toRem(125);
  }

  @media (max-width: toEm(768)) {
    padding-top: toRem(50);
    padding-bottom: toRem(50);
  }

  // .footer__body

  &__body {
    margin-bottom: toRem(15);
    display: grid;
    grid-template-columns: minmax(toRem(150), toRem(200)) minmax(toRem(570), 1fr) minmax(toRem(200), toRem(240));
    gap: toRem(35);
    align-items: start;

    @media (max-width: toEm(1420)) {
      margin-bottom: toRem(40);
      grid-template-columns: auto minmax(toRem(200), toRem(240));
      gap: toRem(50);
    }

    @media (max-width: toEm(1024)) {
      grid-template-columns: 1fr;
    }

    @media (max-width: toEm(768)) {
      gap: toRem(30);
    }
  }

  // .footer__info

  &__info {
    display: flex;
    flex-direction: column;

    @media (max-width: toEm(1420)) {
      grid-column: 1/3;
    }

    @media (max-width: toEm(1024)) {
      grid-column: 1/2;
    }
  }

  // .footer__logo

  &__logo {
    margin-bottom: toRem(32);

    @media (max-width: toEm(1420)) {
      margin-bottom: toRem(20);
    }
  }

  // .footer__text

  &__text {
    font-size: toRem(12);
    line-height: 150%;
  }


  // .footer__menu

  &__menu {
    @media (max-width: toEm(1420)) {}
  }


  // .footer__banner

  &__banner {
    padding: toRem(20);
    display: flex;
    flex-direction: column;
    width: 100%;
    border-radius: 8px;
    background: linear-gradient(10deg, #df87f1 0%, #219f70 100%);
    color: var(--white);
    line-height: 110%;

    @media (max-width: toEm(1024)) {
      width: toRem(240);
    }
  }

  // .footer__banner-logo

  &__banner-logo {
    margin-bottom: toRem(10);
    width: toRem(15);
    height: toRem(21);
    fill: var(--white);
  }

  // .footer__banner-name

  &__banner-name {
    margin-bottom: toRem(5);
    font-weight: 700;
  }

  // .footer__banner-text

  &__banner-text {
    line-height: 120%;
    font-size: toRem(12);
  }

  // .footer__socials-list

  &__socials {
    display: flex;
    align-items: center;
    gap: toRem(10);

    @media (max-width: toEm(1024)) {
      align-self: start;
    }

    @media (max-width: toEm(500)) {
      gap: toRem(15);
    }
  }

  // .footer__socials-item

  &__socials-item {
    &:focus-visible {
      outline: 2px solid var(--icon);
    }
  }

  // .footer__socials-icon

  &__socials-icon {
    width: toRem(24);
    height: toRem(24);
    fill: var(--main-color);
    transition: fill 0.3s ease;

    &:active {
      fill: var(--pink);
    }

    @media (any-hover: hover) {
      &:hover {
        fill: var(--pink);
      }
    }

    @media (max-width: toEm(500)) {
      width: toRem(30);
      height: toRem(30);
    }
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    gap: toRem(30);
    align-items: center;

    @media (max-width: toEm(1024)) {
      flex-direction: column-reverse;
    }
  }

  // .footer__copy
  &__copy {
    font-size: toRem(12);
    line-height: 150%;
  }
}


.menu-footer {

  justify-self: center;
  display: grid;
  gap: toRem(70);
  grid-template-columns: repeat(4, auto);
  justify-content: space-around;

  @media (max-width: toEm(1420)) {
    justify-self: start;
    justify-content: start;
  }

  @media (max-width: toEm(768)) {
    gap: toRem(30);
  }

  @media (max-width: toEm(500)) {
    grid-template-columns: 1fr;
    gap: toRem(15);
    justify-self: stretch;
  }


  // .menu-footer__column

  &__column {
    color: var(--dark-grey);
  }

  // .menu-footer__title

  &__title {
    position: relative;
    margin-bottom: toRem(17);
    font-weight: 700;

    @media (max-width: toEm(500)) {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-size: toRem(20);
    }

    ._init & {
      &::after {
        content: '';
        background: url('../img/svg/arrow.svg') 0 0/cover no-repeat;
        width: toRem(15);
        height: toRem(10);
        transition: transform 0.3s ease;
      }

      &._active {
        &::after {
          transform: rotate(-180deg);
        }
      }
    }

  }

  // .menu-footer__list

  &__list {}

  // .menu-footer__item

  &__item {
    &:not(:last-child) {
      margin-bottom: toRem(10);
    }

    font-size: toRem(13);
    line-height: 120%;

    @media (max-width: toEm(500)) {
      font-size: toRem(16);
    }
  }

  // .menu-footer__link

  &__link {
    transition: color 0.3s ease;

    &:focus-visible {
      outline: 2px solid var(--icon);
    }

    @media (any-hover: hover) {
      &:hover {
        color: var(--pink);
      }
    }
  }
}
