.header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 100;
  background-color: rgba(255, 255, 255, .75);

  @media (min-width:toEm(993)) {
    transform: translate(0px, toRem(-30));
    opacity: 0;
    transition: transform 1.4s ease, opacity 1.4s ease;

    &.loaded {
      transform: translate(0px, 0);
      opacity: 1;
    }
  }


  // .header__body

  &__body {
    padding-top: toRem(10);
    padding-bottom: toRem(10);
    display: grid;
    grid-template-columns: auto 1fr;
    align-items: center;
    column-gap: toRem(20);
    row-gap: toRem(20);
    min-height: toRem(90);
    transition: min-height 0.3s ease, background 0.3s ease;

    @media (max-width: toEm(992)) {
      min-height: toRem(55);
      column-gap: toRem(10);
      grid-template-columns: auto 1fr auto;
    }
  }

  // .header__logo

  &__logo {
    position: relative;
    z-index: 5;
  }

  // .header__menu

  &__menu {
    // justify-self: end;
  }
}



.menu {

  // .menu__body
  &__body {
    display: grid;
    grid-template-columns: 1fr auto;
    justify-content: space-between;
    gap: toRem(30);

    @media (max-width: toEm(992)) {
      padding: toRem(100) toRem(15) toRem(25) toRem(15);
      grid-template-columns: 1fr;
      position: fixed;
      width: 100%;
      height: 100%;
      overflow: auto;
      top: 0;
      left: -100%;
      background-color: #fff;
      transition: left 0.3s ease;

      &::before {
        content: "";
        position: fixed;
        width: 100%;
        height: toRem(55);
        top: 0;
        left: -100%;
        transition: left 0.3s ease 0s;
        background-color: rgba(255, 255, 255, .75);
        z-index: 2;
      }

      &._active {
        left: 0;

        &::before {
          left: 0;
        }
      }
    }
  }

  // .menu__list
  &__list {
    @media (min-width: toEm(993)) {
      display: flex;
      column-gap: toRem(40);
      row-gap: toRem(5);
      flex-wrap: wrap;
      justify-self: center;
    }

    @media (max-width: toEm(1200)) {
      column-gap: toRem(20);
    }

    @media (max-width: toEm(992)) {
      display: flex;
      flex-direction: column;
      row-gap: toRem(15);
    }
  }

  // .menu__item
  &__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    position: relative;

    @media (any-hover: hover) {
      &:hover {
        .menu__link {
          color: var(--pink);
        }

        .menu__arrow-icon {
          stroke: var(--pink);
        }
      }
    }


    @media (min-width: toEm(993)) {
      &.active {
        .menu__sub-list {
          transform: translate(0, 0);
          opacity: 1;
          visibility: visible;
          pointer-events: auto;
        }

        .menu__arrow-icon {
          transform: rotate(-180deg);
          stroke: var(--pink);
        }

        .menu__link {
          color: var(--pink);
        }
      }
    }
  }

  // .menu__link
  &__link {
    display: flex;
    align-items: center;
    gap: toRem(8);
    font-weight: 700;
    font-size: toRem(14);
    line-height: 150%;
    transition: color 0.3s ease;

    &:focus-visible {
      outline: 2px solid var(--icon);
    }

    @media (max-width: toEm(1200)) {
      gap: toRem(4);
    }

    @media (max-width: toEm(992)) {
      font-size: toRem(24);
      font-weight: 600;
      justify-content: space-between;
      width: 100%;
    }

    @media (max-width: toEm(992)) {
      ._init & {
        &._active {
          .menu__arrow-icon {
            transform: rotate(-180deg);
            stroke: var(--pink);
          }
        }
      }
    }
  }


  // .menu__arrow-icon

  &__arrow-icon {
    pointer-events: none;
    width: toRem(9);
    height: toRem(7);
    fill: none;
    stroke-width: 1.12px;
    stroke: var(--main-color);
    transition: stroke 0.3s ease, transform 0.3s ease;

    @media (max-width: toEm(992)) {
      width: toRem(20);
      height: toRem(16);
    }
  }

  // .menu__sub-list
  &__sub-list {
    padding: toRem(15);
    position: relative;
    background: linear-gradient(10deg, #df87f1 0%, #219f70 100%);
    flex: 1 1 100%;

    @media (min-width: toEm(993)) {
      padding: toRem(10);
      pointer-events: none;
      min-width: toRem(200);
      position: absolute;
      left: 0;
      top: 100%;
      z-index: 5;
      border-radius: 8px;
      opacity: 0;
      transform: translate(0, toRem(10));
      visibility: hidden;
      transition: opacity 0.3s ease, transform 0.3s ease;
    }

    @media (max-width: toEm(992)) {
      margin: toRem(10) toRem(-15) 0 toRem(-15);
    }
  }

  // .menu__sub-item
  &__sub-item {
    &:not(:last-child) {
      margin: 0 0 toRem(5) 0;

      @media (max-width: toEm(992)) {
        margin: 0 0 toRem(10) 0;
      }
    }
  }

  // .menu__sub-link
  &__sub-link {
    color: #fff;
    line-height: 120%;
    font-size: toRem(13);

    @media (any-hover: hover) {
      &:hover {
        text-decoration: underline;
      }
    }

    @media (max-width: toEm(992)) {
      font-size: toRem(18);
    }
  }

  &__actions {
    display: grid;
    grid-template-columns: repeat(3, auto);
    align-items: center;
    gap: toRem(10);

    @media (max-width: toEm(992)) {
      align-self: end;
    }

    @media (max-width: toEm(576)) {
      align-self: end;
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__action {
    height: toRem(40);

    @media (max-width: toEm(768)) {
      height: toRem(45);
    }
  }

  &__action--languages {
    min-width: toRem(40);
  }

  &__action-icon {
    width: toRem(20);
    height: toRem(20);
    fill: var(--main-color);
    transition: fill 0.3s ease;
  }

  &__action--login {
    min-width: toRem(120);
  }

  &__action--register {
    min-width: toRem(160);
  }

}



.icon-menu {
  display: none;

  @media (max-width: toEm(992)) {
    position: relative;
    display: block;
    flex: 0 0 toRem(30);
    width: toRem(30);
    height: toRem(18);
    cursor: pointer;
    z-index: 5;

    span {
      top: calc(50% - 1px);
      left: 0;
      position: absolute;
      width: 100%;
      height: 2px;
      background-color: #000;
      transition: transform 0.3s ease;

      &:first-child {
        top: 0;
      }

      &:last-child {
        top: auto;
        bottom: 0;
      }
    }

    &._active {
      span {
        transform: scale(0);

        &:first-child {
          transform: rotate(-45deg);
          top: calc(50% - 1px);
        }

        &:last-child {
          transform: rotate(45deg);
          bottom: calc(50% - 1px);
        }
      }
    }
  }
}