.title {
  font-size: toRem(36);
  font-weight: 700;
  line-height: 110%;
  color: var(--black);
  transform: translate(0px, toRem(30));
  opacity: 0;

  @media (max-width: toEm(768)) {
    font-size: toRem(30);
  }
}


.title.loaded {
  transform: translate(0px, 0);
  opacity: 1;
}

.title.loaded {
  transition: transform 1.4s ease, opacity 1.4s ease;
}


.mb-title {

  margin-bottom: toRem(40);

}

.section-offset {
  padding-top: toRem(70);
  padding-bottom: toRem(70);


  @media (max-width: toEm(768)) {
    padding-top: toRem(50);
    padding-bottom: toRem(50);
  }
}


.logo {
  width: toRem(100);

  img {
    width: 100%;
  }

  &:focus-visible {
    outline: 2px solid var(--icon);
  }
}