//   

.faq__container {
  display: grid;
  grid-template-columns: toRem(310) minmax(toRem(250), toRem(772));
  justify-content: space-between;
  gap: toRem(50);

  @media (max-width: toEm(1024)) {
    grid-template-columns: 1fr;
  }
}


.spollers-faq {}



.spollers-faq__list {}

// 
.spollers-faq__item {
  padding-right: toRem(4);
  border-top: 1px solid #e4e9f0;
  background: #fff;
  overflow: hidden;

  @media (max-width: toEm(768)) {}

  &:last-child {
    border-bottom: 1px solid #e4e9f0;
  }


}

//  
.spollers-faq__title {
  padding: toRem(26) toRem(0) toRem(26) toRem(0);
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: toRem(20);
  width: 100%;
  cursor: default;
  text-align: left;
  background: transparent;
  font-size: toRem(18);
  font-weight: 700;
  line-height: 120%;
  color: var(--black);

  @media (max-width: toEm(768)) {
    padding: toRem(20) toRem(0) toRem(20) toRem(0);
  }

  ._init & {
    cursor: pointer;

    &._active {
      .spollers-faq__icon {
        transform: rotate(-180deg);
        stroke: #fff;
      }

      .spollers-faq__arrow {
        background: linear-gradient(10deg, #df87f1 0%, #219f70 100%);
      }
    }
  }

  &:focus-visible {
    outline: none;

    .spollers-faq__arrow {
      outline: 2px solid var(--icon);
    }
  }
}

.spollers-faq__arrow {
  flex-shrink: 0;
  width: toRem(30);
  height: toRem(30);
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f1f4f7;
  transition: background 0.2s ease;
}

.spollers-faq__icon {
  stroke-width: 1.12px;
  stroke: var(--dark-grey);
  fill: transparent;
  width: toRem(10);
  height: toRem(8);
  transition: transform 0.2s ease, fill 0.2s ease;
}

//  
.spollers-faq__body {
  padding: toRem(0) toRem(62) toRem(26) toRem(0);

  font-size: toRem(14);
  line-height: 140%;
  color: var(--grey);

  @media (max-width: toEm(768)) {
    padding: toRem(0) toRem(62) toRem(20) toRem(0);

  }
}


