.technology {
  position: relative;


  &__body {
    padding: toRem(38) toRem(15);

    position: relative;
    display: flex;
    justify-content: space-between;
    gap: toRem(50);
    align-items: center;
    overflow: hidden;
    background: linear-gradient(73deg, #219f70 0%, #1288aa 50%, #df87f1 100%);
    border-radius: 8px;

    &::before {
      content: "";
      position: absolute;
      z-index: 5;
      top: 0;
      left: 0;
      box-shadow: 90px -50px 150px 50px #a1c665;
    }

    @media (max-width: toEm(992)) {
      justify-content: space-around;
    }

    @media (max-width: toEm(768)) {
      flex-direction: column;
      align-items: stretch;
    }
  }


  &__content {
    margin-left: toRem(60);
    position: relative;
    z-index: 7;

    display: flex;
    flex-direction: column;
    row-gap: toRem(16);
    color: var(--white);

    @media (max-width: toEm(992)) {
      margin-left: 0;
    }
  }

  &__logo {
    width: toRem(29);
    height: toRem(40);
    // transform: translate(0px, toRem(30));
    // opacity: 0;
    // transition: transform 1.4s ease, opacity 1.4s ease;
    fill: var(--white);


    &.loaded {
      transform: translate(0px, 0);
      opacity: 1;
    }
  }

  &__title {
    font-size: toRem(28);
    font-weight: 700;
    line-height: 110%;
    transform: translate(0px, toRem(30));
    opacity: 0;
    transition: transform 1.4s ease, opacity 1.4s ease;

    &.loaded {
      transform: translate(0px, 0);
      opacity: 1;
    }
  }



  &__text {
    max-width: toRem(362);
    font-size: toRem(14);
    line-height: 140%;
    transform: translate(0px, toRem(30));
    opacity: 0;

    &.loaded {
      transform: translate(0px, 0);
      opacity: 1;
      transition: transform 1.6s ease 0.5s, opacity 1.6s ease .5s;
    }
  }

  &__actions {}

  &__action {

    width: toRem(220);
    height: toRem(48);

    // opacity: 0;

    &.loaded {
      opacity: 1;
      transition: opacity 1.7s ease 1s;
    }

    @media (max-width: toEm(500)) {
      width: 100%;
    }
  }


  &__image {
    margin-right: toRem(116);
    flex: 0 0 32.5%;

    img {
      width: 100%;
    }

    @media (max-width: toEm(992)) {
      margin-right: 0;
    }

    @media (max-width: toEm(768)) {
      align-self: center;
      max-width: 90%;
    }
  }


}