.register {
  padding-bottom: toRem(140);

  @media (max-width: toEm(1024)) {
    padding-bottom: toRem(100);
  }

  @media (max-width: toEm(1024)) {}

  // .register__container

  &__container {}

  // .register__body

  &__body {
    padding: toRem(55) toRem(15);

    position: relative;
    display: flex;
    justify-content: center;
    gap: toRem(68);
    align-items: center;
    overflow: hidden;
    background: linear-gradient(73deg, #219f70 0%, #1288aa 50%, #df87f1 100%);
    border-radius: 8px;

    &::before {
      content: "";
      position: absolute;
      z-index: 5;
      top: 0;
      left: 0;
      box-shadow: 90px -50px 150px 50px #a1c665;
    }

    @media (max-width: toEm(1024)) {
      padding: toRem(40) toRem(15);
      gap: toRem(48);
    }

    @media (max-width: toEm(500)) {
      padding: toRem(30) toRem(15);
      gap: toRem(30);
      flex-direction: column;
    }
  }

  // .register__title

  &__title {
    font-weight: 700;
    font-size: toRem(32);
    line-height: 110%;
    color: var(--white);

    @media (max-width: toEm(768)) {
      font-size: toRem(28);
    }

    transform: translate(0px, toRem(30));
    opacity: 0;
    transition: transform 1.4s ease, opacity 1.4s ease;

    &.loaded {
      transform: translate(0px, 0);
      opacity: 1;
    }
  }

  // .register__btn

  &__btn {
    width: toRem(220);
    height: toRem(48);

    @media (max-width: toEm(992)) {
      flex-shrink: 0;
      width: toRem(150);
    }

    @media (max-width: toEm(500)) {
      width: 100%;
    }
  }
}