.security {

  &__container {}

  &__header {}

  &__title {}

  &__text {}

  &__items {
    display: grid;
    grid-template-columns: repeat(4, minmax(toRem(200), 1fr));
    gap: toRem(20);

    @media (max-width: toEm(992)) {
      grid-template-columns: repeat(2, minmax(toRem(200), 1fr));
    }

    @media (max-width: toEm(500)) {
      grid-template-columns: minmax(toRem(200), 1fr);
    }

  }

  &__item {
    display: grid;
    grid-template-rows: toRem(138) toRem(40) auto;
    flex-direction: column;
    opacity: 0;




    &.loaded {
      opacity: 1;
    }

    &--1 {
      transition: opacity 1.4s ease 0s;
    }

    &--2 {
      transition: opacity 1.4s ease 0.2s;
    }

    &--3 {
      transition: opacity 1.4s ease 0.4s;
    }

    &--4 {
      transition: opacity 1.4s ease 0.6s;
    }
  }

  &__image {
    height: toRem(138);
  }


  &__item-title {
    align-self: flex-end;
    margin-bottom: toRem(17);
    color: var(--dark-grey);
    font-size: toRem(18);
    font-weight: 700;
    line-height: 1.1;

    @media (max-width: toEm(768)) {
      font-size: toRem(18);
    }
  }

  &__item-text {
    font-size: toRem(14);
    line-height: 140%;
    color: var(--grey);
  }
}