.button {
  position: relative;
  padding: 0px toRem(7);
  display: inline-flex;
  // width: 100%;
  align-items: center;
  justify-content: center;
  border-radius: 6px;
  background: linear-gradient(10deg, #df87f1 0%, #219f70 100%);
  color: var(--white);
  font-weight: 700;
  text-transform: uppercase;
  position: relative;
  font-size: toRem(12);
  transition: background 0.3s ease, color 0.3s ease;
  z-index: 4;

  &:before {
    border-radius: inherit;
    background: linear-gradient(10deg, #219f70 0%, #df87f1 100%);
    background: linear-gradient(10deg, #df87f1 30%, #219f70 100%);
    content: '';
    display: block;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    width: 100%;
    z-index: -100;
    transition: opacity .3s ease;

  }

  &:focus-visible {
    outline: 2px solid var(--icon);

  }

  &:active {
    outline: none;
  }


  @media (any-hover: hover) {

    &:hover::before {
      opacity: 1;
    }
  }




  &--transparent {
    border: 1px solid var(--main-color);
    background: transparent;
    color: var(--main-color);
    transition: background 0.3s ease, border 0.3s ease, color 0.3s ease;
  }

  &--transparent::before {
    display: none;
  }

  &--transparent:focus-visible {
    outline: 2px solid var(--icon);
  }

  &--transparent:active {
    color: var(--pink);

    svg {
      fill: var(--pink);
    }

    color: var(--pink);
    border: 1px solid var(--pink);
  }

  @media (any-hover: hover) {
    &--transparent:hover {
      svg {
        fill: var(--pink);
      }

      color: var(--pink);
      border: 1px solid var(--pink);

    }
  }




  &--white {
    border: none;
    background: var(--white);
    color: var(--dark-grey);
    transition: background 0.3s ease, border 0.3s ease, color 0.3s ease;

  }

  &--white::before {
    display: none;
  }

  &--white:focus-visible {
    outline: 2px solid var(--icon);
  }

  &--white:active {
    color: var(--pink);

  }

  @media (any-hover: hover) {
    &--white:hover {
      color: var(--pink);
    }
  }
}
