.howitworks {


  &__items {
    display: grid;
    grid-template-columns: repeat(5, minmax(toRem(100), toRem(210)));
    gap: toRem(30);

    @media (max-width: toEm(1200)) {
      gap: toRem(20);
    }

    @media (max-width: toEm(992)) {
      grid-template-columns: repeat(3, minmax(toRem(200), toRem(350)));
    }

    @media (max-width: toEm(768)) {
      grid-template-columns: repeat(2, minmax(toRem(200), toRem(350)));
      gap: toRem(15);
    }

    @media (max-width: toEm(500)) {
      grid-template-columns: minmax(toRem(200), toRem(350));
      gap: toRem(35);
    }

  }

  &__item {
    position: relative;
    background-color: #fff;
    display: grid;
    grid-template-rows: toRem(124) auto 1fr;

    @media (max-width: toEm(768)) {
      grid-template-rows: toRem(120) auto 1fr;
    }

    @media (max-width: toEm(500)) {
      column-gap: toRem(20);
      grid-template-rows: auto;
      grid-template-columns: toRem(80) 1fr;
      align-items: center;
    }


    &:not(:last-child):after {
      content: '';
      top: toRem(60);
      right: 0;
      position: absolute;
      width: 38%;
      height: 1px;
      background-color: var(--light-gray);


      @media (max-width: toEm(992)) {
        display: none;
      }
    }


    &.active {
      .howitworks__image {
        width: toRem(100);
        height: toRem(100);
        background: linear-gradient(25deg, #df87f1 0%, #219f70 100%);

        @media (max-width: toEm(768)) {
          width: toRem(80);
          height: toRem(80);
        }
      }

      .howitworks__image-icon {
        fill: #fff;
      }

      .howitworks__item-text {
        opacity: 1;
      }

      .howitworks__item-title {
        opacity: 1;
      }
    }



    // @media (any-hover: hover) {
    //   &:hover {
    //     .howitworks__image {
    //       width: toRem(100);
    //       height: toRem(100);
    //       background: linear-gradient(10deg, #df87f1 0%, #219f70 100%);

    //       @media (max-width: toEm(768)) {
    //         width: toRem(80);
    //         height: toRem(80);
    //       }
    //     }

    //     .howitworks__image-icon {
    //       fill: #fff;
    //     }

    //     .howitworks__item-title {
    //       opacity: 1;
    //     }

    //     .howitworks__item-text {
    //       opacity: 1;
    //     }
    //   }
    // }
  }

  &__image {
    position: relative;
    margin-bottom: toRem(24);
    align-self: end;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: toRem(80);
    height: toRem(80);
    border-radius: 6px;
    background: var(--background);
    transition: background 0.3s linear,
      width 0.3s linear,
      height 0.3s linear;

    @media (max-width: toEm(768)) {
      margin-bottom: toRem(20);
      width: toRem(60);
      height: toRem(60);
    }

    @media (max-width: toEm(500)) {
      margin-bottom: 0;
      align-self: center;
      justify-self: center;
      grid-row: 1 / 3;
    }
  }

  &__image-icon {
    width: toRem(40);
    height: toRem(40);
    fill: var(--grey);
    transition: fill 0.3s linear;

    @media (max-width: toEm(768)) {
      width: toRem(30);
      height: toRem(30);
    }

    &--1 {
      width: toRem(58);
      height: toRem(26);

      @media (max-width: toEm(768)) {
        width: toRem(48);
        height: toRem(20);
      }
    }

  }

  &__item-title {
    margin-bottom: toRem(15);

    font-size: toRem(18);
    font-weight: 800;
    color: var(--dark-grey);
    opacity: 0.7;
    transition: opacity 0.3s ease;

    @media (max-width: toEm(768)) {
      font-size: toRem(16);
    }

    @media (max-width: toEm(500)) {
      align-self: end;
    }
  }

  &__item-text {
    font-size: toRem(14);
    font-weight: 500;
    line-height: 140%;
    color: var(--black);
    opacity: 0.5;
    transition: opacity 0.3s ease;

    @media (max-width: toEm(992)) {
      font-size: toRem(13);
    }

    @media (max-width: toEm(500)) {
      align-self: start;
    }
  }

}
