.products {

  // .products__container

  &__container {}

  // .products__title

  &__title {}

  // .products__list

  &__list {
    display: flex;
    gap: toRem(20);

    @media (max-width: toEm(768)) {
      flex-direction: column;
    }
  }

  // .products__item

  &__item {}
}



.item-products {
  cursor: pointer;
  position: relative;
  padding: toRem(355) toRem(40) toRem(40);
  flex: 1;
  border-radius: 6px;
  background-color: var(--background);
  display: flex;
  flex-direction: column;
  height: toRem(486);
  overflow: hidden;
  background-position: toRem(40) toRem(36);
  background-repeat: no-repeat;
  opacity: 0.5;
  transition: flex 0.3s ease, opacity 0.3s ease;

  @media (max-width: toEm(1024)) {
    padding: toRem(315) toRem(30) toRem(20);
    height: toRem(466);
  }

  @media (max-width: toEm(768)) {
    padding: toRem(300) toRem(20) toRem(20);
    background-position: toRem(20) toRem(20);
    opacity: 1;
    height: auto;
  }

  &.active {
    opacity: 1;
  }

  &--1 {
    background-size: toRem(292);
    background-image: url('../img/products/01.svg');

    @media (max-width: toEm(1024)) {
      background-size: toRem(252);
    }
  }

  &--2 {
    background-size: toRem(328);
    background-image: url('../img/products/02.svg');

    @media (max-width: toEm(1024)) {
      background-size: toRem(280);
    }
  }

  &--3 {
    background-size: toRem(420);
    background-image: url('../img/products/03.svg');

    @media (max-width: toEm(1024)) {
      background-size: toRem(380);
    }
  }


  // .item-products__body

  &__body {
    display: flex;
    flex-direction: column;
    min-height: 100%;
  }

  // .item-products__title

  &__title {
    margin-bottom: toRem(11);
    font-weight: 400;
    font-size: toRem(24);
    line-height: 120%;
    color: var(--black);

    @media (max-width: toEm(1024)) {
      font-size: toRem(20);
    }

    .active & {
      font-weight: 800;
    }

    @media (max-width: toEm(768)) {
      font-weight: 800;
    }
  }

  // .item-products__text

  &__text {
    max-width: 75%;
    font-weight: 500;
    line-height: 140%;
    color: var(--black);

    opacity: 0;
    visibility: hidden;
    transform: translate(toRem(-40), 0);
    transition: opacity 0.3s ease 0.2s, transform 0.3s ease 0.2s;

    .active & {
      opacity: 1;
      visibility: visible;
      transform: translate(0, 0);

    }

    @media (max-width: toEm(768)) {
      font-size: toRem(14);
      opacity: 1;
      visibility: visible;
      transform: translate(0, 0);
    }
  }

  // .item-products__link

  &__link {
    pointer-events: none;
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    transition: opacity 0.3s ease;

    .active & {
      pointer-events: auto;
      opacity: 1;
    }

    &:focus-visible {
      outline: none;

      .item-products__link-icon {
        outline: 2px solid var(--icon);
      }
    }

    &:active {
      .item-products__link-icon {
        fill: var(--icon);
      }
    }

    @media (max-width: toEm(768)) {
      pointer-events: auto;
      opacity: 1;
    }


  }

  // .item-products__link-icon

  &__link-icon {
    position: absolute;
    bottom: toRem(55);
    right: toRem(40);
    width: toRem(41);
    height: toRem(16);

    fill: var(--pink);
    transition: fill 0.3s ease;

    @media (any-hover: hover) {
      &:hover {
        fill: var(--icon);
      }
    }

    @media (max-width: toEm(1024)) {
      bottom: toRem(45);
      right: toRem(30);
    }

    @media (max-width: toEm(768)) {
      width: toRem(35);
      height: toRem(14);
      bottom: toRem(20);
      right: toRem(20);
    }
  }

  &.active {
    flex: 2;

  }

  @media (max-width: toEm(1024)) {
    &.active {
      flex: 3;
    }
  }
}
