//  
@use "sass:math";

@import "vars";
@import "mixins";
@import "fonts";
@import "settings";




body {
  &._lock {
    overflow: hidden;
    touch-action: none;
    overscroll-behavior: none;
  }

  //  
  .loaded & {}
}



.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;


  @supports (overflow: clip) {
    overflow: clip;
  }

  //  footer
  >main {
    flex: 1 1 auto;
  }

  //   
  >* {
    min-width: 0;
  }
}


[class*="__container"] {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-inner-width);
}


.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}



