.hero {
  position: relative;
  padding-top: toRem(180);
  padding-bottom: toRem(60);

  @media (max-width: toEm(992)) {
    padding-top: toRem(150);

  }

  @media (max-width: toEm(768)) {
    padding-top: toRem(90);
    padding-bottom: toRem(20);
  }

  &__container {}

  &__main {
    margin-bottom: toRem(80);
    display: flex;
    justify-content: space-between;
    align-items: center;

    @media (max-width: toEm(768)) {
      flex-direction: column;
      align-items: stretch;
    }
  }


  &__content {


    padding-right: toRem(110);

    display: flex;
    flex-direction: column;

    row-gap: toRem(40);

    @media (max-width: toEm(992)) {
      padding-right: toRem(50);
      row-gap: toRem(30);
    }

    @media (max-width: toEm(768)) {
      padding-right: 0;
      margin-bottom: toRem(50);
      row-gap: toRem(25);
    }

    @media (max-width: toEm(500)) {}
  }


  &__title {
    font-size: toRem(48);
    font-weight: 800;
    line-height: 110%;
    transform: translate(0px, toRem(30));
    opacity: 0;
    transition: transform 1.4s ease, opacity 1.4s ease;
    color: var(--black);

    &.loaded {
      transform: translate(0px, 0);
      opacity: 1;
    }


    @media (max-width: toEm(992)) {
      font-size: toRem(36);
    }

    @media (max-width: toEm(768)) {
      font-size: toRem(36);
      // max-width: toRem(500);
    }
  }

  &__title-color {
    background: linear-gradient(62deg, #df87f1 0%, #259e73 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

    @media (max-width: toEm(768)) {
      display: block;
    }
  }

  &__text {
    max-width: toRem(475);
    font-size: toRem(16);
    line-height: 140%;
    transform: translate(0px, toRem(30));
    opacity: 0;

    &.loaded {
      transform: translate(0px, 0);
      opacity: 1;
      transition: transform 1.6s ease 0.5s, opacity 1.6s ease .5s;
    }

    @media (max-width: toEm(1460)) {
      max-width: toRem(500);
    }

    @media (max-width: toEm(768)) {
      max-width: toRem(400);
    }
  }

  &__actions {}

  &__action {
    width: toRem(220);
    height: toRem(64);
    opacity: 0;

    &.loaded {
      opacity: 1;
      transition: opacity 1.7s ease 1s;
    }


    @media (max-width: toEm(992)) {

      height: toRem(60);
    }

    @media (max-width: toEm(768)) {
      width: 100%;
      height: toRem(55);
    }
  }


  &__image {

    flex: 0 0 40.5%;

    img {
      width: 100%;
    }

    @media (max-width: toEm(768)) {
      align-self: center;
      max-width: 90%;
      // width: 90%;
    }
  }


}


.advantages {

  // .advantages__list

  &__list {
    padding: toRem(18) 0;
    border-radius: 6px;
    display: grid;
    grid-template-columns: repeat(4, auto);
    background: #f4f6ff;

    @media (max-width: toEm(576)) {
      grid-template-columns: repeat(2, 1fr);
      padding: 0 toRem(15);
      column-gap: toRem(15);
      // row-gap: toRem(20);
    }
  }

  // .advantages__item

  &__item {
    display: flex;
    justify-content: center;
    padding: 0 toRem(5);

    &:not(:last-child) {
      border-right: 1px solid #ccd3f5;
    }

    @media (max-width: toEm(576)) {
      padding: toRem(10) 0;
      justify-content: start;

      &:not(:last-child) {

        border-right: none;
      }

      &:nth-child(1),
      &:nth-child(2) {
        border-bottom: 1px solid #ccd3f5;
      }

      // justify-content: stretch;
    }

  }

  &__item-content {
    font-weight: 600;
    font-size: toRem(14);
    line-height: 130%;
    color: var(--black);
    max-width: toRem(155);

    span {
      display: block;
    }

    @media (max-width: toEm(992)) {
      font-size: toRem(13);
      // padding-bottom: toRem(10);
    }

    @media (max-width: toEm(576)) {
      font-size: toRem(12);
      max-width: 100%;
      text-align: start;

      // span {
      //   display: inline-block;
      // }

      // display: flex;
      // flex-direction: column;
      // align-items: center;
    }

  }
}
